<template>
    <div class="regi-wrap">
        <div class="container">
            <div class="order-list">
                <h2 class="order-list-ttl">注文リスト</h2>
                <div class="order-list-content" v-if="orders.items">
                    <span class="table-name">{{ orders.table }}</span>
                    <ul id="orderItems">
                        <li class="item-list" v-for="order in orders.items" :key="order.id">
                            <span class="item-name">{{ order.name }}</span>
                            <span class="item-about">¥ {{ order.price }} &times; {{ order.amount }}</span>
                            <span class="trash-icon" @click="removeItem(order)"><i class="fas fa-trash-alt"></i></span>
                        </li>
                    </ul>
                </div>
                <section class="order-list-content" v-else>
                    <h3>お会計待ちの状態ではないか、選択されていません</h3>
                </section>
                <div class="order-list-footer">
                    <div class="total">
                        <span id="totalItems">点数: {{ amount }}</span>
                        <span id="totalAmount">小計: ¥{{ bill }}</span>
                    </div>
                    <div class="alter-btn" v-if="orders.items">
                        <button class="delete" v-if="orders.items.length <= 0" @click="resetPayment">取引中止</button>
                    </div>
                </div>
            </div>


            <div class="table-actions-wrapper">
                <div class="table-list">
                    <h2 class="table-list-ttl">テーブル</h2>
                    <table>
                        <thead>
                            <tr>
                                <th>テーブル番号</th>
                                <th>合計金額</th>
                                <th>ステータス</th>
                            </tr>
                        </thead>
                        <tbody id="tableBody" v-if="tables.length > 0">
                            <tr v-for="(table, value) in tables" :key="value" :class="'table-line table' + (value + 1)" @click="getAbout(table)">
                                <td class="table-name">{{ table.number }}</td>
                                <td class="table-total">¥{{ table.total }}</td>
                                <td v-if="table.status === 'お会計待ち'" class="billing">{{ table.status }}</td>
                                <td v-else>{{ table.status }}</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <div class="checkout-buttons">
                    <button class="checkout-btn" id="checkoutBtn" @click="showPayment">支払いへ進む</button>
                </div>
            </div>
        </div>


        <!-- 支払いモーダルウィンドウ -->
        <div id="paymentModal" class="modal">
            <div class="modal-content">
                <span class="close" @click="closePayment">&times;</span>
                <h2 class="payment-ttl">支払い画面</h2>
                <div class="payment-detail">
                    <p v-if="bill != 0" class="totalPayment">合計金額 : <span class="total-label">¥{{ bill }}</span></p>
                    <p v-else>何も注文していません</p>
                    <div class="payment-input">
                    <!-- <label for="totalAmountToPay" v-if="total != 0"></label>
                        <input type="text" id="totalAmountToPay" readonly /><br> -->
                        <label for="amountReceived">お預かり金額 : </label>
                        <input type="number" id="amountReceived" v-model="received" min="0"/>
                        <!-- <div id="changeDue">お釣り: ¥<span id="changeAmount">0</span></div> -->
                    </div>
                    <p class="changeDue" v-if="received - bill >= 0">お釣り: ¥{{ received - bill }}</p>
                    <p class="changeDue" v-else>お釣り: ¥0</p>
                    <button class="confirmPayment" v-if="received - bill >= 0" @click="finishPayment">確定</button>
                    <button class="confirmPayment disable" v-else>確定</button>
                </div>
                <div id="confirmationMessage" class="confirmation-message">
                    <p>
                        お会計ありがとうございました。<br>
                        お会計金額 : ¥{{ bill }}<br>
                        お釣り金額 : ¥{{ received - bill }}
                    </p>
                </div>
                <!-- メッセージ表示用 -->
            </div>
        </div>
        <!-- /支払いモーダルウィンドウ -->
    </div>
</template>

<script>
import Ajax from '@/lib/Ajax';

export default {
    data() {
        return {
            tables: [],
            orders: {},
            bill: 0, // 合計金額
            amount: 0, // 合計注文個数
            received: 0, // お預かり金額
            interval: null
        }
    },
    methods: {
        getTables() {
            Ajax('/api/master/regi/tables')
            .then(res => res.json())
            .then(dat => {
                this.tables = dat;
            });
        },

        getAbout(table) {
            this.amount = 0;
            this.$el.querySelector('.payment-detail').classList.remove('hide');
            this.$el.querySelector('.confirmation-message').classList.add('hide');
            if(table.status === 'お会計待ち') {
                Ajax(`/api/master/regi/order/detail/${table.id}`) // テーブル名で検索をかける感じ
                .then(res => res.json())
                .then(dat => {
                    this.orders = dat;
                    this.orders.items.forEach(item => {
                        this.amount += item.amount;
                    });
                    this.bill = dat.total;
                });

                this.received = 0;
            } else {
                this.bill = 0;
                this.orders = {};
            }
        },

        showPayment() {
            if(this.orders.table) {
                this.$el.querySelector('#paymentModal').style.display = 'block';
            }
        },

        closePayment() {
            this.$el.querySelector('#paymentModal').style.display = '';
        },

        finishPayment() {
            // 選択された行を検索する
            const table = this.tables.filter(dat => dat.number === this.orders.table)[0];
            if(table) {
                table.bill = this.received;
                Ajax('/api/master/regi/accounting', 'POST', table);
                this.getTables();
            } else {
                console.log('テーブル名が見つかりませんでした。');
            }


            // 表示部の処理
            this.$el.querySelector('.payment-detail').classList.add('hide');
            this.$el.querySelector('.confirmation-message').classList.remove('hide');
        },

        resetPayment() {
            const table = this.tables.filter(dat => dat.number === this.orders.table)[0];
            if(table) {
                Ajax(`/api/master/regi/accounting/${table.id}`, 'PUT');
                setTimeout(this.getTables, 100);
            } else {
                console.log('テーブル名が見つかりませんでした。');
            }
        },

        removeItem(order) {
            if(order) {
                const amo = order.amount;
                const total = order.price * amo;
                const filtered = this.orders.items.filter(dat => dat != order);
                this.orders.items = filtered;
                this.amount -= amo;
                this.bill -= total;

                // const delData = {
                //     table: this.orders.table,
                //     item: order.id
                // };

                Ajax(`/api/master/regi/items/${order.id}`, 'DELETE');
            }
        }
    },
    mounted() {
        this.getTables();
        this.interval = setInterval(this.getTables, 5000);
        this.$emit('setDisplay', 'regi');
    },
    beforeUnmount() {
        if(this.interval) {
            clearInterval(this.interval);
        }
    },
    emits:[
        'setDisplay'
    ]
}
</script>

<style>
.order-item {
    font-size: 1.5rem;
}
</style>
